.main_div {
height: 100vh;
widows: 100vw;
display: flex;
justify-content: center;
align-items: center;
flex-direction: column;
gap: 1rem;
}
.main_input {
height: 2rem;
width: 15rem;
padding: 5px;
}
.main_button {
height: 2rem;
width: 15rem;
display: flex;
justify-content: center;
align-items: center;
}
.loading_spinner {
border: 4px solid rgba(0, 0, 0, 0.3);
border-top: 4px solid #2196f3;
border-radius: 50%;
width: 20px;
height: 20px;
animation: spin 1s linear infinite;
}
@keyframes spin {
0% {
transform: rotate(0deg);
}
100% {
transform: rotate(360deg);
}
}
